    <el-tab-pane label="企业赞助" name="zanzhu"><zanzhu></zanzhu></el-tab-pane>
    <el-tab-pane label="校园活动" name="huodong"><huodong></huodong></el-tab-pane>
    <el-tab-pane label="学校社团" name="shetuan"><shetuan></shetuan></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import zixun from '@/components/zixun'
import zanzhu from '@/components/zanzhu'
import huodong from '@/components/huodong'
import shetuan from '@/components/shetuan'
export default {
  data () {
    return {
      activeName: 'zanzhu'
    }
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    }
  },
  components: {
    zixun,
    zanzhu,
    huodong,
    shetuan
  }
}

</script>
<style>
#con2{
  border-top:lightgrey solid 1px;
}
</style>



